import React from 'react';
import $ from 'jquery';
import { Link } from 'react-router-dom';
import './RaiseButton.scss';

export class RaiseButton extends React.Component {
  componentDidMount(): void {
    $(window).scroll(() => {
      if (($(window).scrollTop() ?? 0) > 320) {
        $('.raise-container').addClass('shown');
      } else {
        $('.raise-container').removeClass('shown');
      };
    });

    $('.raise-container').click(() => {
      $('html, body').animate({ scrollTop: 0 }, 300);
    });
  };

  render() {
    return (
      <div className="raise-container">
        <div className="text-colored raise-icon">
          <i className="fa-solid fa-chevron-up"></i>
        </div>
      </div>
    );
  };
};