import React from 'react';
import $ from 'jquery';
import { Link } from 'react-router-dom';
import { ServerServices, AppServices, Product } from '../services';
import './Catalog.scss'

import { RaiseButton } from '../components/RaiseButton';
import { Cart } from '../components/Cart';

export class Catalog extends React.Component {
  componentDidMount(): void {
    const setFilterLogic = (products: any, oldMinCost: number, oldMaxCost: number): void => {
      const changeInput = (inputs: Array<{ element: JQuery; isShown: boolean; isEmpty: boolean }>): void => {
        for (let i = 0; i < inputs.length; i++) {
          const { element, isShown, isEmpty } = inputs[i];
          if (isEmpty) { element.empty() }
          else if (element.find('option[value="all"]').length === 0) { element.append(`<option value="all"></option>`); element.val('all') };
          if (isShown) { element.closest('.filter').addClass('shown') }
          else if (!isShown) { element.closest('.filter').removeClass('shown') };
        };
      };

      const typeElement: JQuery = $('select[name="filter-type"]');
      const nameElement: JQuery = $('select[name="filter-name"]');
      const sortElement: JQuery = $('select[name="filter-sort"]');
      const producerElement: JQuery = $('select[name="filter-producer"]');
      const pricepoolMinValueElement = $('.filter-pricepool-value-minimum');
      const pricepoolMaxValueElement = $('.filter-pricepool-value-maximum');
      const minCostElement: JQuery = $('.filter-pricepool-inputs-container').children('input').eq(0);
      const maxCostElement: JQuery = $('.filter-pricepool-inputs-container').children('input').eq(1);
      const resetElement: JQuery = $('.filters-reset');
      const applyElement: JQuery = $('.filters-apply');

      let type: string = 'all';
      let name: string = 'all';
      let sort: string = 'all';
      let producer: string = 'all';
      let minCost: number = oldMinCost;
      let maxCost: number = oldMaxCost;

      pricepoolMinValueElement.text(`${oldMinCost} `);
      pricepoolMaxValueElement.text(`${oldMaxCost} `);

      minCostElement.on('keypress', (event: any) => {
        if (event.key === 'Enter') {
          const value: string = (event.target as HTMLInputElement).value;
          minCost = Number(value);
          changePricePoolByInput(oldMinCost, minCost, oldMaxCost, maxCost);
        };
      });

      minCostElement.on('blur', (event: any) => {
        const value: string = (event.target as HTMLInputElement).value;
        minCost = Number(value);
        changePricePoolByInput(oldMinCost, minCost, oldMaxCost, maxCost);
      });

      maxCostElement.on('keypress', (event: any) => {
        if (event.key === 'Enter') {
          const value: string = (event.target as HTMLInputElement).value;
          maxCost = Number(value);
          changePricePoolByInput(oldMinCost, minCost, oldMaxCost, maxCost);
        };
      });

      maxCostElement.on('blur', (event: any) => {
        const value: string = (event.target as HTMLInputElement).value;
        maxCost = Number(value);
        changePricePoolByInput(oldMinCost, minCost, oldMaxCost, maxCost);
      });

      changeInput([
        { element: typeElement, isShown: true, isEmpty: true },
        { element: nameElement, isShown: false, isEmpty: true },
        { element: sortElement, isShown: false, isEmpty: true },
        { element: producerElement, isShown: false, isEmpty: true }
      ]);

      for (let i = 0; i < products.length; i++) {
        const product = products[i];

        if (i < 1) { typeElement.append(`<option value="all"></option>`) };

        if (product.type !== '') {
          if (!typeElement.find(`option[value="${product.type}"]`).length) {
            typeElement.append(`<option value="${product.type}">${product.displayed_type.charAt(0).toUpperCase() + product.displayed_type.slice(1)}</option>`);
          };
        };
      };

      typeElement.closest('.filter').addClass('shown');
      typeElement.change((): void => {
        const selectedType = $(typeElement).find('option:selected').val();
        type = selectedType as string;

        nameElement.change((): void => {
          const selectedName = $(nameElement).find('option:selected').val();
          name = selectedName as string;
        });

        sortElement.change((): void => {
          const selectedSort = $(sortElement).find('option:selected').val();
          sort = selectedSort as string;
        });

        producerElement.change((): void => {
          const selectedProducer = $(producerElement).find('option:selected').val();
          producer = selectedProducer as string;
        });

        switch (selectedType) {
          case 'all':
            changeInput([
              { element: nameElement, isShown: false, isEmpty: true },
              { element: sortElement, isShown: false, isEmpty: true },
              { element: producerElement, isShown: false, isEmpty: true }
            ]);
            break;
          default:
            const types: Array<any> = products.filter((product: any) => product.type === selectedType);

            changeInput([
              { element: nameElement, isShown: true, isEmpty: false },
            ]);

            for (let i = 0; i < types.length; i++) {
              if (types[i].name !== '') {
                if (!nameElement.find(`option[value="${types[i].name}"]`).length) {
                    nameElement.append(`<option value="${types[i].name}">${types[i].displayed_name.charAt(0).toUpperCase() + types[i].displayed_name.slice(1)}</option>`);
                };
              };
            };
            const sorts: Array<any> = types.filter((product: any) => product.name);

            changeInput([
              { element: sortElement, isShown: true, isEmpty: false },
            ]);

            for (let i = 0; i < sorts.length; i++) {
              if (sorts[i].sort !== '') {
                if (!sortElement.find(`option[value="${sorts[i].sort}"]`).length) {
                    sortElement.append(`<option value="${sorts[i].sort}">${sorts[i].displayed_sort.charAt(0).toUpperCase() + sorts[i].displayed_sort.slice(1)}</option>`);
                };
              };
            };
            const producers: Array<any> = types.filter((product: any) => product.producer);

            changeInput([
              { element: producerElement, isShown: true, isEmpty: false },
            ]);

            for (let i = 0; i < sorts.length; i++) {
              if (sorts[i].producer !== '') {
                if (!producerElement.find(`option[value="${producers[i].producer}"]`).length) {
                    producerElement.append(`<option value="${producers[i].producer}">${producers[i].displayed_producer.charAt(0).toUpperCase() + producers[i].displayed_producer.slice(1)}</option>`);
                };
              };
            };
            break;
        };
      });

      resetElement.click(() => {
        type = 'all';
        name = 'all';
        sort = 'all';
        producer = 'all';
        changeInput([
          { element: typeElement, isShown: true, isEmpty: false },
          { element: nameElement, isShown: false, isEmpty: true },
          { element: sortElement, isShown: false, isEmpty: true },
          { element: producerElement, isShown: false, isEmpty: true }
        ]);
        typeElement.val('all');
        minCost = oldMinCost;
        maxCost = oldMaxCost;
        changePricePool(oldMinCost, oldMinCost, oldMaxCost, oldMaxCost);
        AppServices.getMinMaxProductsCost(products).then(({ minCost, maxCost }) => { showAppliedFilters($('.applieds-container'), oldMinCost, oldMaxCost, { type: typeElement.find('option:selected'), name: nameElement.find('option:selected'), sort: sortElement.find('option:selected'), producer: producerElement.find('option:selected'), minCost: minCost, maxCost: maxCost }) });
        showFilteredProducts($('.products-container'), $('.products-empty-container'), products, { type: type, name: name, sort: sort, producer: producer, minCost: oldMinCost, maxCost: oldMaxCost });
      });

      applyElement.click(() => {
        changePricePoolByInput(oldMinCost, minCost, oldMaxCost, maxCost);
        showAppliedFilters($('.applieds-container'), oldMinCost, oldMaxCost, { type: typeElement.find('option:selected'), name: nameElement.find('option:selected'), sort: sortElement.find('option:selected'), producer: producerElement.find('option:selected'), minCost: minCost, maxCost: maxCost });
        showFilteredProducts($('.products-container'), $('.products-empty-container'), products, { type: type, name: name, sort: sort, producer: producer, minCost: minCost, maxCost: maxCost });
      });

      changePricePool(oldMinCost, minCost, oldMaxCost, maxCost);
      showAppliedFilters($('.applieds-container'), oldMinCost, oldMaxCost, { type: typeElement.find('option:selected'), name: nameElement.find('option:selected'), sort: sortElement.find('option:selected'), producer: producerElement.find('option:selected'), minCost: minCost, maxCost: maxCost });
      showFilteredProducts($('.products-container'), $('.products-empty-container'), products, { type: type, name: name, sort: sort, producer: producer, minCost: minCost, maxCost: maxCost });
    };

    const showFilteredProducts = (containerElement: JQuery, emptyContainerElement: JQuery, products: any, filters: any): void => {
      containerElement.empty();

      for (const product of products) {
        if (product.type === filters.type || filters.type === 'all') {
          if (product.name === filters.name || filters.name === 'all') {
            if (product.sort === filters.sort || filters.sort === 'all') {
              if (product.producer === filters.producer || filters.producer === 'all') {
                if (product.cost >= filters.minCost && product.cost <= filters.maxCost) {
                  const productElement: JQuery = Product.setOptions(Product.getElement(product), (product: any) => {
                    Cart.addProduct(product, cartTotalCost => {})
                  });
                  
                  containerElement.append(productElement);
                };
              };
            };
          };
        };
      };

      if (containerElement.children().length > 0) {
        containerElement.addClass('shown');
        emptyContainerElement.removeClass('shown');
      } else {
        containerElement.removeClass('shown');
        emptyContainerElement.addClass('shown');
      };
    };

    const showAppliedFilters = (containerElement: JQuery, oldMinCost: number, oldMaxCost: number, filters: any): void => {
      containerElement.empty();

      if (filters.type.val() !== 'all' && filters.type.length > 0) { containerElement.append(`<div class="applied"><span class="applied-value">${filters.type.text().charAt(0).toUpperCase() + filters.type.text().slice(1)}</span></div>`) };
      if (filters.name.val() !== 'all' && filters.name.length > 0) { containerElement.append(`<div class="applied"><span class="applied-value">${filters.name.text().charAt(0).toUpperCase() + filters.name.text().slice(1)}</span></div>`) };
      if (filters.sort.val() !== 'all' && filters.sort.length > 0) { containerElement.append(`<div class="applied"><span class="applied-value">${filters.sort.text().charAt(0).toUpperCase() + filters.sort.text().slice(1)}</span></div>`) };
      if (filters.producer.val() !== 'all' && filters.producer.length > 0) { containerElement.append(`<div class="applied"><span class="applied-value">${filters.producer.text().charAt(0).toUpperCase() + filters.producer.text().slice(1)}</span></div>`) };
      if (filters.minCost !== oldMinCost || filters.maxCost !== oldMaxCost) {
        containerElement.append(`<div class="applied"><span class="applied-value">${filters.minCost} - ${filters.maxCost} </span></div>`)
      };

      if (containerElement.children().length > 0) { containerElement.addClass('shown') }
      else { containerElement.removeClass('shown') };
    };

    const changePricePool = (oldMinCost: number, minCost: number, oldMaxCost: number, maxCost: number) => {
      const calculatedMinCost = ((minCost - oldMinCost) / (oldMaxCost - oldMinCost)) * 100;
      const minControlLeft = (calculatedMinCost / 100) * $('.filter-pricepool-field-container').width()!;
      const calculatedMaxCost = ((maxCost - oldMinCost) / (oldMaxCost - oldMinCost)) * 100;
      const maxControlLeft = (calculatedMaxCost / 100) * $('.filter-pricepool-field-container').width()!;

      $('.filter-pricepool-input').eq(0).val(minCost);
      $('.filter-pricepool-input').eq(1).val(maxCost);

      $('.filter-pricepool-control-minimum').css('left', `${minControlLeft}px`);
      $('.filter-pricepool-control-maximum').css('left', `${maxControlLeft - 18}px`);

      $('.filter-pricepool-field-active').css('left', `${$('.filter-pricepool-control-minimum').position().left}px`);
      $('.filter-pricepool-field-active').css('width', `${$('.filter-pricepool-control-maximum').position().left - $('.filter-pricepool-control-minimum').position().left + 18}px`);
    };

    const changePricePoolByInput = (oldMinCost: number, minCost: number, oldMaxCost: number, maxCost: number): void => {
      if (isNaN(minCost)) { minCost = oldMinCost };
      if (isNaN(maxCost)) { maxCost = oldMaxCost };
      if (minCost < oldMinCost) { minCost = oldMinCost };
      if (maxCost > oldMaxCost) { maxCost = oldMaxCost };
      if (minCost > maxCost) { if (maxCost < oldMinCost) { maxCost = minCost }; minCost = maxCost };

      changePricePool(oldMinCost, minCost, oldMaxCost, maxCost);
    };

    jQuery(() => {
      ServerServices.getProducts().then((products: any) => {
        AppServices.getMinMaxProductsCost(products).then(({ minCost, maxCost }) => {
          setFilterLogic(products, minCost, maxCost);
        });
      });
    });
  };

  render() {
    return (
      <React.Fragment>
        <div className="section">
          <Cart totalCost={0} totalDiscount={0} discountTypes={[]} />
          <div className="filters-container">
            <div className="filter">
              <span className="filter-name"></span>
              <select name='filter-type' className="filter-select"></select>
            </div>
            <div className="filter">
              <span className="filter-name"></span>
              <select name='filter-name' className="filter-select"></select>
            </div>
            <div className="filter">
              <span className="filter-name"></span>
              <select name='filter-sort' className="filter-select"></select>
            </div>
            <div className="filter">
              <span className="filter-name"></span>
              <select name='filter-producer' className="filter-select"></select>
            </div>
            <div className="filter shown">
              <span className="filter-name"></span>
              <div className="filter-pricepool-value-container">
                <span className="filter-pricepool-value-minimum">0 </span>
                <span className="filter-pricepool-value-maximum">0 </span>
              </div>
              <div className="filter-pricepool-container">
                <div className="filter-pricepool-field-container">
                  <div className="filter-pricepool-field-active"></div>
                </div>
                <div className="filter-pricepool-control-minimum">
                  <div></div>
                </div>
                <div className="filter-pricepool-control-maximum">
                  <div></div>
                </div>
              </div>
              <div className="filter-pricepool-inputs-container">
                <input type="text" name='filter-pricepool-input-minimum' className="filter-pricepool-input" maxLength={12} defaultValue={0}></input>
                <input type="text" name='filter-pricepool-input-maximum' className="filter-pricepool-input" maxLength={12} defaultValue={0}></input>
              </div>
            </div>
            <div className="button filters-reset">
              <span>
                <i className="fa-solid fa-filter-circle-xmark"></i>
              </span>
              <span></span>
            </div>
            <div className="button text-colored filters-apply">
              <span>
                <i className="fa-solid fa-filter"></i>
              </span>
              <span></span>
            </div>
          </div>
          <div className="applieds-container"></div>
          <div className="products-container"></div>
          <div className="products-empty-container">
            <span className='products-empty-icon'>
              <i className="fa-solid fa-magnifying-glass"></i>
            </span>
            <span className='products-empty-text'>
                
            </span>
          </div>
        </div>

        <RaiseButton />
      </React.Fragment>
    );
  };
};