import React, { useEffect } from 'react';
import { createRoot } from 'react-dom/client';
import { BrowserRouter } from 'react-router-dom';
import { Provider } from 'react-redux';
import { store } from './store';
import { AuthorizedApp } from './AuthorizedApp';
import { UnauthorizedApp } from './UnauthorizedApp';
import { useSelector } from 'react-redux';
import { RootState } from './store';

const App = () => {
  const isAuthenticated = useSelector((state: RootState) => state.auth.isAuthenticated);

  useEffect(() => { setTheme('light') }, [isAuthenticated]);

  return isAuthenticated ? <AuthorizedApp /> : <UnauthorizedApp />;
};

const setTheme = (theme: 'light' | 'dark') => {
  const themes = {
    light: {
      $main: 'rgb(250, 250, 250)',
      $primary: 'rgba(230, 230, 230, 0.8)',
      $element: 'rgba(0, 0, 0, 0.05)',
      $additional: 'rgb(30, 130, 105)',
      $inverted: 'rgb(0, 0, 0)',
      $name: 'rgb(0, 0, 0)',
      $title: 'rgb(30, 30, 30)',
      $subtitle: 'rgb(70, 70, 70)',
      $description: 'rgb(110, 110, 110)',
      $link: 'rgb(0, 150, 250)'
    },
    dark: {
      $main: 'rgb(0, 0, 0)',
      $primary: 'rgba(20, 20, 20, 0.8)',
      $element: 'rgba(250, 250, 250, 0.05)',
      $additional: 'rgb(30, 130, 105)',
      $inverted: 'rgb(250, 250, 250)',
      $name: 'rgb(250, 250, 250)',
      $title: 'rgb(220, 220, 220)',
      $subtitle: 'rgb(180, 180, 180)',
      $description: 'rgb(140, 140, 140)',
      $link: 'rgb(0, 150, 250)'
    }
  };

  const selectedTheme = themes[theme];
  Object.keys(selectedTheme).forEach((key: string) => {
    const cssKey = `--color-${key.slice(1)}`;
    const cssValue = selectedTheme[key as keyof typeof selectedTheme];

    console.log(`${cssKey}: ${cssValue}`);
    document.documentElement.style.setProperty(cssKey, cssValue as string);
  });
};

const rootElement = document.getElementById('root');
if (!rootElement) throw new Error('Failed to find the root element');
const root = createRoot(rootElement);

root.render(
  <Provider store={store}>
    <BrowserRouter basename='/'>
      <App />
    </BrowserRouter>
  </Provider>
);