import React from 'react';
import $ from 'jquery';
import { Link } from 'react-router-dom';
import { ServerServices } from '../services';
import './Cart.scss';

interface CartProps {
  totalCost: number;
  totalDiscount: number;
  discountTypes: Array<any>;
};

export class Cart extends React.Component<CartProps> {
  static cartProductsCheck = () => {
    if ($('.cart-products-container').children().length > 0) {
      const getPromocode = (request: string): void => {
        const value = request.toUpperCase();

        ServerServices.getPromocodes().then((promocodes) => {
          let found = false;
          for (let i = 0; i < promocodes.length; i++) {
            const promocode = promocodes[i];
            setTimeout(() => {
              if (found) return;
              if (promocode.code === value) {
                Cart.addPromocode(promocode,
                  (discount, types) => {
                    const totalDiscountElement = $('.cart-cost-sum-discounted');
                    const totalCostElement = $('.cart-cost-sum-clear');
                    
                    totalDiscountElement.text(`${Math.round(Cart.getTotalCost() - (Cart.getTotalCost() * (Cart.getTotalDiscount() / 100)))} `);
                    totalCostElement.text(`${Math.round(Cart.getTotalCost())} `);
                    
                    if (Cart.getTotalDiscount() > 0) {
                      totalDiscountElement.addClass('selected');
                      totalCostElement.addClass('secondary');
                    } else {
                      totalDiscountElement.removeClass('selected');
                      totalCostElement.removeClass('secondary');
                    };
                  }
                );
                $('.cart-promocode-results-container').addClass('shown');
                found = true;
              };
            }, 200);
          };
          if (!found) {
            $('.cart-promocode-results-container').removeClass('shown');
            $('.cart-promocode-results-container').empty();
          };
        }).catch((error) => {
          console.error('Error fetching promocodes:', error);
        });
      };

      $('.cart-promocode-field').on('input', (e) => {
        const value = $(e.currentTarget).val() as string;

        clearTimeout((e.currentTarget as any).inputTimeout);
        (e.currentTarget as any).inputTimeout = setTimeout(() => {
          Cart.clearPromocodesDiscount(
            (discount, types) => {
              const totalDiscountElement = $('.cart-cost-sum-discounted');
              const totalCostElement = $('.cart-cost-sum-clear');

              totalDiscountElement.text(`${Math.round(Cart.getTotalCost() - (Cart.getTotalCost() * (Cart.getTotalDiscount() / 100)))} `);
              totalCostElement.text(`${Math.round(Cart.getTotalCost())} `);

              if (Cart.getTotalDiscount() > 0) {
                totalDiscountElement.addClass('selected');
                totalCostElement.addClass('secondary');
              } else {
                totalDiscountElement.removeClass('selected');
                totalCostElement.removeClass('secondary');
              };
            }
          );
        }, 50);

        getPromocode(value);
      });

      $('.cart-container').addClass('shown');
    } else if ($('.cart-products-container').children().length === 0) {
      $('.cart-container').removeClass('shown');
    };
  };

  static getTotalCost(): number {
    const cartProductsContainer = $('.cart-products-container');
    let totalCost = 0;
    cartProductsContainer.children().each((_, product) => {
      const cost = Math.round(parseFloat($(product).find('.cart-product-cost').text().replace(' ', '')));
      totalCost += cost;
    });
    return totalCost;
  };

  static addProduct(product: any, updateTotalCost: (cost: number) => void): void {
    const cartProductsContainer = $('.cart-products-container');

    const existingProduct = cartProductsContainer.children().filter((_, productElement) => {
      const $productElement = $(productElement);
      return (
        $productElement.find('.cart-product-type-name').text().toLowerCase() === product.displayed_name.toLowerCase() &&
        $productElement.find('#product-characteristic-type').text().toLowerCase() === product.displayed_type.toLowerCase() &&
        $productElement.find('#product-characteristic-sort').text().toLowerCase() === product.displayed_sort.toLowerCase() &&
        $productElement.find('#product-characteristic-producer').text().toLowerCase() === product.displayed_producer.toLowerCase()
      );
    });

    if (existingProduct.length > 0) {
      const setTotalCost = () => {
        const totalCostElement = $('.cart-cost-sum-clear');
        const discountedCostElement = $('.cart-cost-sum-discounted');

        totalCostElement.text(`${Math.round(Cart.getTotalCost())} `);
        discountedCostElement.text(`${Math.round(Cart.getTotalCost() - (Cart.getTotalCost() * (Cart.getTotalDiscount() / 100)))} `);
      };

      const input = existingProduct.find('.cart-product-weight-value');
      const currentWeight = parseInt(input.val() as string) || 0;
      const newWeight = currentWeight + product.current_weight;

      if (newWeight > product.max_weight) {
        input.val(product.max_weight);
      } else {
        input.val(newWeight);
      }

      const validWeight = parseInt(input.val() as string);
      existingProduct.find('.cart-product-cost').text(`${validWeight * product.cost} `);
      updateTotalCost(Math.round(validWeight * product.cost));
      setTotalCost();
      return;
    } else if (existingProduct.length === 0) {
      cartProductsContainer.append(`
        <div class="cart-product">
          <div class="cart-product-type-icon">
            <i class="fa-solid fa-cannabis"></i>
          </div>
          <div class="cart-product-type-name">${product.displayed_name.charAt(0).toUpperCase() + product.displayed_name.slice(1)}</div>
          <div class="cart-product-characteristics-container">
            <div class="cart-product-characteristic" id="product-characteristic-type">${product.displayed_type.charAt(0).toUpperCase() + product.displayed_type.slice(1)}</div>
            <div class="cart-product-characteristic" id="product-characteristic-sort">${product.displayed_sort.charAt(0).toUpperCase() + product.displayed_sort.slice(1)}</div>
            <div class="cart-product-characteristic" id="product-characteristic-producer">${product.displayed_producer.charAt(0).toUpperCase() + product.displayed_producer.slice(1)}</div>
            <div class="cart-product-characteristic">
              <span id="product-characteristic-thc">${product.thc}%</span>
              &nbsp;&nbsp;
              <span id="product-characteristic-cbd">${product.cbd}%</span>
            </div>
            <div class="cart-product-characteristic" id="product-characteristic-quantity">${product.quantity} .</div>
          </div>
          <div class="cart-product-weight">
            <div class="cart-product-weight-container">
              <div class="cart-product-weight-remove">
                <i class="fa-solid fa-minus"></i>
              </div>
              <input type="text" class="cart-product-weight-value" defaultValue={"0"} maxLength={4}></input>
              <div class="cart-product-weight-add">
                <i class="fa-solid fa-plus"></i>
              </div>
            </div>
            <div class="cart-product-cost">${product.cost} </div>
            <div class="cart-product-remove">
              <i class="fa-solid fa-trash-can"></i>
            </div>
          </div>
        </div>
      `);

      const lastProduct = cartProductsContainer.children().last();

      const setTotalCost = () => {
        const totalCostElement = $('.cart-cost-sum-clear');
        const discountedCostElement = $('.cart-cost-sum-discounted');

        totalCostElement.text(`${Math.round(Cart.getTotalCost())} `);
        discountedCostElement.text(`${Math.round(Cart.getTotalCost() - (Cart.getTotalCost() * (Cart.getTotalDiscount() / 100)))} `);
      };

      const setCurrentProductWeight = () => {
        const currentWeight = product.current_weight;
        const input = lastProduct.find('.cart-product-weight-value');

        if (currentWeight < product.min_weight) {
          input.val(product.min_weight);
        } else if (currentWeight > product.max_weight) {
          input.val(product.max_weight);
        } else {
          input.val(currentWeight);
        };

        const validWeight = parseInt(input.val() as string);
        lastProduct.find('.cart-product-cost').text(`${validWeight * product.cost} `);
        updateTotalCost(Math.round(validWeight * product.cost));
        setTotalCost();
      };

      const updateProductWeight = () => {
        const input = lastProduct.find('.cart-product-weight-value');
        let value = parseInt(input.val() as string);

        if (isNaN(value)) {
          value = product.min_weight;
        };
        input.val(value);

        if (value < product.min_weight) {
          value = product.min_weight;
        } else if (value > product.max_weight) {
          value = product.max_weight;
        };
        if (value <= product.min_weight) {
          lastProduct.find('.cart-product-weight-remove').addClass('disabled');
        } else {
          lastProduct.find('.cart-product-weight-remove').removeClass('disabled');
        };
        if (value >= product.max_weight) {
          lastProduct.find('.cart-product-weight-add').addClass('disabled');
        } else {
          lastProduct.find('.cart-product-weight-add').removeClass('disabled');
        };
        input.val(value);
        const validWeight = parseInt(input.val() as string);
        lastProduct.find('.cart-product-cost').text(`${value * product.cost} `);
        updateTotalCost(Math.round(validWeight * product.cost));
        setTotalCost();
      };

      const decreaseProductWeight = () => {
        const input = lastProduct.find('.cart-product-weight-value');
        let value = parseInt(input.val() as string);
        if (value > product.min_weight) {
          value--;
          input.val(value);
          const validWeight = parseInt(input.val() as string);
          lastProduct.find('.cart-product-cost').text(`${value * product.cost} `);
          updateTotalCost(Math.round(validWeight * product.cost));
          setTotalCost();
        };
        if (value <= product.min_weight) {
          lastProduct.find('.cart-product-weight-remove').addClass('disabled');
        };
        lastProduct.find('.cart-product-weight-add').removeClass('disabled')
      };

      const increaseProductWeight = () => {
        const input = lastProduct.find('.cart-product-weight-value');
        let value = parseInt(input.val() as string);
        if (value < product.max_weight) {
          value++;
          input.val(value);
          const validWeight = parseInt(input.val() as string);
          lastProduct.find('.cart-product-cost').text(`${value * product.cost} `);
          updateTotalCost(Math.round(validWeight * product.cost));
          setTotalCost();
        };
        if (value >= product.max_weight) {
          lastProduct.find('.cart-product-weight-add').addClass('disabled');
        };
        lastProduct.find('.cart-product-weight-remove').removeClass('disabled')
      };

      if (product.sort === '') {
        lastProduct.find('#product-characteristic-sort').remove();
      };

      if (product.producer === '') {
        lastProduct.find('#product-characteristic-producer').remove();
      };

      if (product.thc === 0 && product.cbd === 0) {
        lastProduct.find('#product-characteristic-thc').parent().remove();
      };

      if (product.quantity === 1) {
        lastProduct.find('#product-characteristic-quantity').remove();
      };

      setCurrentProductWeight();
      updateProductWeight();

      lastProduct.find('.product-sort-description-title').click(() => {
        lastProduct.find('.product-sort-description-container').toggleClass('shown');
        lastProduct.find('.product-sort-description-title i').toggleClass('shown');
      });

      lastProduct.find('.product-characteristics-title').click(() => {
        lastProduct.find('.product-characteristics-container').toggleClass('shown');
        lastProduct.find('.product-characteristics-title i').toggleClass('shown');
      });

      lastProduct.find('.cart-product-weight-remove').click(() => {
        decreaseProductWeight();
      });

      let removeInterval: NodeJS.Timeout;
      let removeIntervalDelay = 200;

      lastProduct.find('.cart-product-weight-remove').mousedown(() => {
        let elapsed = 0;

        const startInterval = () => {
          removeInterval = setInterval(() => {
            elapsed += removeIntervalDelay;

            if (elapsed > 750) {
              clearInterval(removeInterval); //   
              removeIntervalDelay = 100; //   
              startInterval(); //      
            }

            decreaseProductWeight();
          }, removeIntervalDelay);
        };

        startInterval();
      }).mouseup(() => {
        clearInterval(removeInterval);
        removeIntervalDelay = 200; //  
      }).mouseleave(() => {
        clearInterval(removeInterval);
        removeIntervalDelay = 200; //  
      });

      lastProduct.find('.cart-product-weight-value').on('input', () => {
        const input = lastProduct.find('.cart-product-weight-value');
        let value = parseInt(input.val() as string);

        if (isNaN(value)) {
          value = product.min_weight;
        };
        input.val(value);
        let validWeight = parseInt(input.val() as string);
        lastProduct.find('.cart-product-cost').text(`${value * product.cost} `);
        updateTotalCost(Math.round(validWeight * product.cost));
        setTotalCost();

        if (value < product.min_weight) {
          value = product.min_weight;
        } else if (value > product.max_weight) {
          value = product.max_weight;
        }
        input.val(value);
        validWeight = parseInt(input.val() as string);
        lastProduct.find('.cart-product-cost').text(`${value * product.cost} `);
        updateTotalCost(Math.round(validWeight * product.cost));
        setTotalCost();
      });

      lastProduct.find('.cart-product-weight-add').click(() => {
        increaseProductWeight();
      });

      let addInterval: NodeJS.Timeout;
      let addIntervalDelay = 200;

      lastProduct.find('.cart-product-weight-add').mousedown(() => {
        let elapsed = 0;

        const startInterval = () => {
          addInterval = setInterval(() => {
            elapsed += addIntervalDelay;
            if (elapsed > 750 && addIntervalDelay !== 100) {
              clearInterval(addInterval); //   
              addIntervalDelay = 100; //   
              startInterval(); //      
              return;
            }

            increaseProductWeight();
          }, addIntervalDelay);
        };

        startInterval();
      }).mouseup(() => {
        clearInterval(addInterval);
        addIntervalDelay = 200; //  
      }).mouseleave(() => {
        clearInterval(addInterval);
        addIntervalDelay = 200; //  
      });

      lastProduct.find('.cart-product-remove').click(() => {
        const validWeight = parseInt(lastProduct.find('.cart-product-weight-value').val() as string) || product.min_weight;

        updateTotalCost(Math.round(-validWeight * product.cost));
        lastProduct.remove();

        if (cartProductsContainer.children().length === 0) { $('.cart-container').removeClass('shown') };

        // Recalculate the total cost after removing the product
        const newTotalCost = Cart.getTotalCost();
        updateTotalCost(Math.round(newTotalCost));
        setTotalCost();
      });
    };

    Cart.cartProductsCheck();
  };

  static getTotalDiscount(): number {
    const cartPromocodesContainer = $('.cart-promocode-results-container');
    let totalDiscount = 0;
    cartPromocodesContainer.children('.selected').each((_, promocode) => {
      const discount = Math.round(parseFloat($(promocode).find('.cart-promocode-result-info-discount').text().replace('-', '').replace('%', '')));
      totalDiscount += discount;
    });
    return totalDiscount;
  };

  static addPromocode(promocode: any, updateTotalDiscount: (discount: number, types: Array<any>) => void): void {
    $('.cart-promocode-results-container').empty();
    $('.cart-promocode-results-container').append(`
      <div class="cart-promocode-result">
        <div class="cart-promocode-result-radio-container">
          <div class="cart-promocode-result-radio"></div>
        </div>
        <div class="cart-promocode-result-info-container">
          <div class="cart-promocode-result-info-code">
            <span>${promocode.code}</span>
            <span class="cart-promocode-result-info-discount">-${promocode.discount}%</span>
          </div>
          <span class="cart-promocode-result-info-description"></span>
        </div>
      </div>
    `);

    const currentTarget = $('.cart-promocode-result').last();

    const enablePromocode = ($target: JQuery) => {
      $target.parent().parent().find('.cart-promocode-field').addClass('hidden');
      $target.parent().parent().find('.cart-promocode-field').prop('disabled', true);
      $target.parent().addClass('raised');
      $target.find('.cart-promocode-result').removeClass('selected');
      $target.find('.cart-promocode-result-radio').removeClass('selected');
      $target.addClass('selected');
      $target.find('.cart-promocode-result-radio').addClass('selected');

      updateTotalDiscount(promocode.discount, promocode.products.types);
    };

    const disablePromocode = ($target: JQuery) => {
      $target.parent().parent().find('.cart-promocode-field').removeClass('hidden');
      $target.parent().parent().find('.cart-promocode-field').prop('disabled', false);
      $target.parent().removeClass('raised');
      $target.removeClass('selected');
      $target.find('.cart-promocode-result-radio').removeClass('selected');

      updateTotalDiscount(0, []);
    };

    if (promocode.products.types.length === 1) {
      currentTarget.find('.cart-promocode-result-info-description').text(`
               ${promocode.products.types[0].displayed_type}
        `);
    } else if (promocode.products.types.length > 1) {
      currentTarget.find('.cart-promocode-result-info-description').text(`
               ${promocode.products.types.map((product: any) => product.displayed_type).join(', ')}
        `);
    };

    currentTarget.click((e) => {
      const $target = $(e.currentTarget);
      if ($target.hasClass('selected')) { disablePromocode($target) }
      else { enablePromocode($target) };
    });
  };

  static clearPromocodesDiscount = (updateTotalDiscount: (discount: number, types: Array<any>) => void): void => {
    updateTotalDiscount(0, []);
  };

  componentDidMount() {
    jQuery(() => {
      Cart.cartProductsCheck();
    });
  };

  render() {
    return (
      <div className="cart-container">
        <div className="cart-products-container"></div>
        <div className="cart-promocode-container">
          <span className="cart-promocode-title">   ?</span>
          <div className="cart-promocode-search-container">
            <input type='text' className="cart-promocode-field" maxLength={24} placeholder='XXXX' />
            <div className="cart-promocode-results-container"></div>
          </div>
        </div>
        <div className="cart-cost-container">
          <div className="cart-cost-name">  </div>
          <div className="cart-cost-sum-container">
            <div className="cart-cost-sum-discounted">0 </div>
            <div className="cart-cost-sum-clear">0 </div>
          </div>
        </div>
        <div className="cart-actions-container">
          <div className="button text-colored cart-action-apply">
            <span>
              <i className="fa-solid fa-gift"></i>
            </span>
            <span>
               
            </span>
          </div>
        </div>
      </div>
    );
  };
};