import $ from 'jquery';

const SERVER = 'http://localhost:5000';

export class ServerServices {
  static getProducts = async () => {
    try {
      const response = await $.ajax({
        url: `${SERVER}/products`,
        method: 'GET',
      });
      return response.data;
    } catch (error) {
      console.error('Error fetching products:', error);
      throw error;
    };
  };
  static getPromocodes = async () => {
    try {
      const response = await $.ajax({
        url: `${SERVER}/promocodes`,
        method: 'GET',
      });
      return response.data;
    } catch (error) {
      console.error('Error fetching promocodes:', error);
      throw error;
    };
  };
};

export class AppServices {
  static getMinMaxProductsCost = async (products: Array<any>) => {
    let minCost = 0;
    let maxCost = 0;

    for (const product of products) {
      if (minCost === 0 || product.cost < minCost) {
        minCost = product.cost;
      };
      if (product.cost > maxCost) {
        maxCost = product.cost;
      };
    };

    return { minCost, maxCost };
  };
};

export class Product {
  static getElement = (product: any): any => {
    const productElement = (`
      <div class="product">
        <div class="product-type">
          <span class='product-type-icon'></span>
          <span class='product-type-name'>${product.name}</span>
        </div>
        <div class="product-sort-description-title">
          <span> </span>
          <i class="fa-solid fa-angle-down"></i>
        </div>
        <div class="product-sort-description-container">
          <span class="product-sort-description-text">${product.description}</span>
          <div class="product-sort-description-actions-container">
            <a href='${product.wikipedia}' target="_blank" class="button product-sort-description-action"
              id="product-wikipedia">
              <i class="fa-brands fa-wikipedia-w"></i>
            </a>
          </div>
        </div>
        <div class="product-characteristics-title">
          <span></span>
          <span class="product-characteristics-icon">
            <i class="fa-solid fa-angle-down"></i>
          </span>
        </div>
        <div class="product-characteristics-container">
          <div class="product-characteristic" id="product-characteristic-type">
            <span class="product-characteristic-name"></span>
            <span class="product-characteristic-value">${product.displayed_type.charAt(0).toUpperCase() + product.displayed_type.slice(1)}</span>
          </div>
          <div class="product-characteristic" id="product-characteristic-sort">
            <span class="product-characteristic-name"></span>
            <span class="product-characteristic-value">${product.displayed_sort.charAt(0).toUpperCase() + product.displayed_sort.slice(1)}</span>
          </div>
          <div class="product-characteristic" id="product-characteristic-name">
            <span class="product-characteristic-name"></span>
            <span class="product-characteristic-value">${product.displayed_producer.charAt(0).toUpperCase() + product.displayed_producer.slice(1)}</span>
          </div>
          <div class="product-characteristic small" id="product-characteristic-thc">
            <div class="product-characteristic-name">THC</div>
            <div class="product-characteristic-value">${product.thc}%</div>
          </div>
          <div class="product-characteristic small" id="product-characteristic-cbd">
            <span class="product-characteristic-name">CBD</span>
            <span class="product-characteristic-value">${product.cbd}%</span>
          </div>
          <div class="product-characteristic" id="product-characteristic-quantity">
            <span class="product-characteristic-name"></span>
            <span class="product-characteristic-value">${product.quantity} .</span>
          </div>
        </div>
        <div class="product-cost-container">
          <div class="product-cost-weight-container">
            <div class="product-cost-weight-remove">
              <i class="fa-solid fa-minus"></i>
            </div>
            <input type="text" maxLength="4" value="0" class="product-cost-weight-value"></input>
            <div class="product-cost-weight-add">
              <i class="fa-solid fa-plus"></i>
            </div>
          </div>
          <span class="product-cost">0 </span>
        </div>
        <div class="button text-colored product-view">
          <span>
            <i class="fa-solid fa-cart-shopping"></i>
          </span>
          <span>  </span>
        </div>
      </div>
    `);

    return { product: product, $productElement: $(productElement) };
  };
  static setOptions = ({ product, $productElement }: { product: { [key: string]: any }, $productElement: JQuery<HTMLElement> }, toCartFunction: (product: any) => void): JQuery => {
    const resetProductWeight = ($productElement: JQuery) => {
      const input = $productElement.find('.product-cost-weight-value');
      input.val(product.min_weight);
      
      updateProductWeight($productElement);
    };
    
    const updateProductWeight = ($productElement: JQuery) => {
      const input = $productElement.find('.product-cost-weight-value');
      let value = parseInt(input.val() as string);

      if (isNaN(value)) {
        value = product.min_weight;
      };
      input.val(value);

      if (value < product.min_weight) {
        value = product.min_weight;
      } else if (value > product.max_weight) {
        value = product.max_weight;
      };
      if (value <= product.min_weight) {
        $productElement.find('.product-cost-weight-remove').addClass('disabled');
      } else {
        $productElement.find('.product-cost-weight-remove').removeClass('disabled');
      };
      if (value >= product.max_weight) {
        $productElement.find('.product-cost-weight-add').addClass('disabled');
      } else {
        $productElement.find('.product-cost-weight-add').removeClass('disabled');
      };
      input.val(value);
      $productElement.find('.product-cost').text(`${value * product.cost} `);
    };

    const decreaseProductWeight = ($productElement: JQuery) => {
      const input = $productElement.find('.product-cost-weight-value');
      let value = parseInt(input.val() as string);
      if (value > product.min_weight) {
        value--;
        input.val(value);
        $productElement.find('.product-cost').text(`${value * product.cost} `);
      };
      if (value <= product.min_weight) {
        $productElement.find('.product-cost-weight-remove').addClass('disabled');
      };
      $productElement.find('.product-cost-weight-add').removeClass('disabled')
    };

    const increaseProductWeight = ($productElement: JQuery) => {
      const input = $productElement.find('.product-cost-weight-value');
      let value = parseInt(input.val() as string);
      if (value < product.max_weight) {
        value++;
        input.val(value);
        $productElement.find('.product-cost').text(`${value * product.cost} `);
      };
      if (value >= product.max_weight) {
        $productElement.find('.product-cost-weight-add').addClass('disabled');
      };
      $productElement.find('.product-cost-weight-remove').removeClass('disabled')
    };

    let iconClass: string;
    let removeInterval: NodeJS.Timeout;
    let removeIntervalDelay = 200;
    let addInterval: NodeJS.Timeout;
    let addIntervalDelay = 200;

    if (product.wikipedia === '') { $productElement.find('#product-wikipedia').remove() };
    if (product.sort === '') { $productElement.find('#product-characteristic-sort').remove() };
    if (product.producer === '') { $productElement.find('#product-characteristic-producer').remove() };
    if (product.thc === 0) { $productElement.find('#product-characteristic-thc').remove() };
    if (product.cbd === 0) { $productElement.find('#product-characteristic-cbd').remove() };
    if (product.quantity === 1) { $productElement.find('#product-characteristic-quantity').remove() };

    if (product.max_weight >= product.min_weight) { $productElement.find('.product-cost-weight-value').val(product.min_weight) }
    else {
      // Not enough product, disable the input
    }

    switch (product.type) {
      case 'heads': iconClass = 'fa-solid fa-cannabis'; break;
      case 'hashish': iconClass = 'fa-solid fa-cannabis'; break;
      case 'antidepressants': iconClass = 'fa-solid fa-pills'; break;
      case 'anticonvulsants': iconClass = 'fa-solid fa-pills'; break;
      case 'tranquilizers': iconClass = 'fa-solid fa-pills'; break;
      default: iconClass = 'fa-solid fa-question';
    };

    $productElement.find('.product-type-icon').append(`<i class="${iconClass}"></i>`);

    $productElement.find('.product-sort-description-title').click(() => {
      $productElement.find('.product-sort-description-container').toggleClass('shown');
      $productElement.find('.product-sort-description-title i').toggleClass('shown');
    });

    $productElement.find('.product-characteristics-title').click(() => {
      $productElement.find('.product-characteristics-container').toggleClass('shown');
      $productElement.find('.product-characteristics-title i').toggleClass('shown');
    });

    $productElement.find('.product-cost-weight-remove').click(() => {
      decreaseProductWeight($productElement);
    });

    $productElement.find('.product-cost-weight-remove').mousedown(() => {
      let elapsed = 0;

      const startInterval = () => {
        removeInterval = setInterval(() => {
          elapsed += removeIntervalDelay;

          if (elapsed > 750) {
            clearInterval(removeInterval); //   
            removeIntervalDelay = 100; //   
            startInterval(); //      
          }

          decreaseProductWeight($productElement);
        }, removeIntervalDelay);
      };

      startInterval();
    }).mouseup(() => {
      clearInterval(removeInterval);
      removeIntervalDelay = 200; //  
    }).mouseleave(() => {
      clearInterval(removeInterval);
      removeIntervalDelay = 200; //  
    });

    $productElement.find('.product-cost-weight-value').on('input', () => {
      const input = $productElement.find('.product-cost-weight-value');
      let value = parseInt(input.val() as string);

      if (isNaN(value)) {
        value = product.min_weight;
      };
      input.val(value);

      if (value < product.min_weight) {
        value = product.min_weight;
      } else if (value > product.max_weight) {
        value = product.max_weight;
      }
      input.val(value);
      $productElement.find('.product-cost').text(`${value * product.cost} `);
    });

    $productElement.find('.product-cost-weight-add').click(() => {
      increaseProductWeight($productElement);
    });

    $productElement.find('.product-cost-weight-add').mousedown(() => {
      let elapsed = 0;

      const startInterval = () => {
        addInterval = setInterval(() => {
          elapsed += addIntervalDelay;
          if (elapsed > 750 && addIntervalDelay !== 100) {
            clearInterval(addInterval); //   
            addIntervalDelay = 100; //   
            startInterval(); //      
            return;
          }

          increaseProductWeight($productElement);
        }, addIntervalDelay);
      };

      startInterval();
    }).mouseup(() => {
      clearInterval(addInterval);
      addIntervalDelay = 200; //  
    }).mouseleave(() => {
      clearInterval(addInterval);
      addIntervalDelay = 200; //  
    });

    $productElement.find('.product-view').click(() => {
      const input = $productElement.find('.product-cost-weight-value');
      let value = parseInt(input.val() as string);
      if (isNaN(value)) {
        value = product.min_weight;
      };
      input.val(value);

      if (value < product.min_weight) {
        value = product.min_weight;
      } else if (value > product.max_weight) {
        value = product.max_weight;
      };
      input.val(value);

      const productCharacteristics = {
        id: product.id,
        type: product.type,
        displayed_type: product.displayed_type,
        name: product.name,
        displayed_name: product.displayed_name,
        description: product.description,
        sort: product.sort,
        displayed_sort: product.displayed_sort,
        producer: product.producer,
        displayed_producer: product.displayed_producer,
        thc: product.thc,
        cbd: product.cbd,
        wikipedia: product.wikipedia,
        quantity: product.quantity,
        min_weight: product.min_weight,
        max_weight: product.max_weight,
        current_weight: value,
        cost: product.cost
      };

      toCartFunction(productCharacteristics);
      resetProductWeight($productElement);
    });

    updateProductWeight($productElement);
    return $productElement;
  };
};